import React, {CSSProperties} from 'react';
import './Button.scss';
import Icon from 'react-eva-icons';

export interface Props {
    children?: object | string;
    /**
     * Set button appearance type
     *
     * 'solid' type = 1st high priority button
     *
     * 'link' type = new tap or new window
     */
    type?: 'solid' | 'line' | 'text' | 'link';
    color?: 'blue' | 'yellow' | 'green' | 'red';
    size?: 'xs' | 'small' | 'middle' | 'large';
    /**
     * Search in https://akveo.github.io/eva-icons/#/
     */
    iconName?: string;
    iconSize?: string | number;
    onClick?: any
    disabled?: boolean
    style?: CSSProperties;
    /**
     * If you use Tooltip, set 'true'
     */
    dataTip?: boolean
    /**
     * If you use Tooltip, set Tooltip ID key.
     */
    dataFor?: string;
    className?: string;
}


/**
 * Priority : solid > line > text
 *
 * Solid type  ,  ,     
 *
 * Link type ,      
 *
 * iconName   https://akveo.github.io/eva-icons/#/
 */
const Button = ({
                    children = '',
                    type = 'solid',
                    color = 'blue',
                    size = 'middle',
                    iconName = '',
                    iconSize = '18',
                    onClick,
                    disabled = false,
                    style,
                    dataTip,
                    dataFor,
                    className
                }: Props) => {
    const iconButton =
        <button
            style={style}
            onClick={onClick}
            className={`btn-type-${type} btn-color-${color} btn-size-${size} ${disabled ? 'btn-disabled' : ''} btn-icon-text ${className}`}
            data-tip={dataTip}
            data-for={dataFor}
        >
            <Icon
                name={iconName}
                size={iconSize}
            />
            {(iconName && children !== '') && <span>{children}</span>}
        </button>

    const normalButton =
        <button
            style={style}
            disabled={disabled}
            onClick={onClick}
            className={`btn-type-${type} btn-color-${color} btn-size-${size} ${disabled ? 'btn-disabled' : ''} ${className}`}
            data-tip={dataTip}
            data-for={dataFor}
        >
            <span>
                {children}
            </span>
        </button>

    return (
        iconName ? iconButton : normalButton
    )
}

export default Button
